@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primaryColor: #f15025;
  --mainWhite: #fff;
  --mainBlack: #222;
  --lightGrey: #eee;
  --mainGrey: #9e9e9e;
  --mainRed: #d50000;
  --mainGreen: #4caf50;
}
* {
  margin: 0;
  box-sizing: border-box;
}
body {
  background: #9e9e9e;
  background: var(--mainGrey);
  color: #222;
  color: var(--mainBlack);
  font-family: "Roboto", sans-serif;
}

#root {
  min-width: 700px;
  overflow: scroll;
  margin: 0 auto;
}

h1 {
  text-align: center;
  text-transform: capitalize;
  margin: 2rem 0;
  color: #fff;
  color: var(--mainWhite);
  letter-spacing: 5px;
}
.App {
  display: flex;
  margin: 0 auto;
  overflow: scroll;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background: #fff;
  background: var(--mainWhite);
  padding: 2rem;
  border-radius: 2px;
}

.prime-table {
  margin: 0 auto;
  font-size: 20px;
  overflow: scroll;
}

button {
  border: none;
  background: inherit;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.select-n {
  margin: 0 auto;
  text-align: center;
  margin: 2rem 0;
  letter-spacing: 5px;
  font-size: 28px;
  font: bolder;
}

.form-control {
  border: none;
  background: transparent;
  border-bottom: 1px solid #9e9e9e;
  border-bottom: 1px solid var(--mainGrey);
  border-radius: 0;
  outline: none;
  width: 3.6rem;
  height: 2rem;
  font-size: 28px;
  margin: 0 0 8px 0;
  padding: 10px;
}

.form-control:hover {
  background: #fff;
  background: var(--mainWhite);
}

.nrow {
  width: 40px;
  height: 20px;
  margin: 0 auto;
}

.odd-row,
.even-row {
  display: flex;
}

.odd-row:hover,
.even-row:hover {
  background: #9e9e9e;
  background: var(--mainGrey);
}

.even-row {
  background: #eee;
  background: var(--lightGrey);
}

.error {
  margin: 0 auto;
  font-size: 20px;
  color: red;
  text-align: center;
}

.error-adv {
  color: #222;
  color: var(--mainBlack);
  padding: 5px;
}

.border-bottom {
  border-bottom: 2px solid #222;
  border-bottom: 2px solid var(--mainBlack);
}

.nrow:first-child {
  border-right: 2px solid #222;
  border-right: 2px solid var(--mainBlack);
}

